import React, { useState, useEffect, useMemo } from 'react';
import { initializeApp } from 'firebase/app';
import { getAuth, signInAnonymously, onAuthStateChanged, signInWithCustomToken } from 'firebase/auth';
// Removed unused imports: doc, setDoc, getDoc
import { getFirestore, collection, addDoc, onSnapshot, query } from 'firebase/firestore'; 
import { setLogLevel } from 'firebase/firestore';

// --- Helper function to format date and time ---
const formatDate = (date) => {
    if (!date) return 'N/A';
    // Converts Firestore timestamp to JS Date, then to a readable string
    // Added more robust check for .toDate() function presence
    const jsDate = date.toDate && typeof date.toDate === 'function' ? date.toDate() : new Date(date);
    return jsDate.toLocaleDateString('en-US', {
        weekday: 'long', year: 'numeric', month: 'long', day: 'numeric'
    });
};

const formatTime = (timeString) => {
    if (!timeString) return 'N/A';
    const [hour, minute] = timeString.split(':');
    const h = parseInt(hour, 10);
    const suffix = h >= 12 ? 'PM' : 'AM';
    const h12 = ((h + 11) % 12 + 1);
    return `${h12}:${minute} ${suffix}`;
};


// --- Main App Component ---
export default function App() {
    // --- State Management ---
    const [db, setDb] = useState(null);
    const [auth, setAuth] = useState(null);
    const [userId, setUserId] = useState(null);
    const [isAuthReady, setIsAuthReady] = useState(false);

    const [appointments, setAppointments] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);
    const [successMessage, setSuccessMessage] = useState('');
    // Added isSubmitting state to prevent double submissions and provide feedback
    const [isSubmitting, setIsSubmitting] = useState(false);

    // --- Form State ---
    const [formData, setFormData] = useState({
        name: '',
        phone: '',
        email: '',
        address: '',
        serviceType: 'General Notary Work',
        date: '',
        time: '',
        // Added otherServiceType for conditional input
        otherServiceType: '', 
    });

    // --- Firebase Configuration ---
    // These variables are provided by the environment.
    const appId = typeof __app_id !== 'undefined' ? __app_id : 'default-notary-app';
    const firebaseConfig = useMemo(() => {
        try {
            return typeof __firebase_config !== 'undefined' ? JSON.parse(__firebase_config) : {};
        } catch (e) {
            console.error("Failed to parse Firebase config:", e);
            return {};
        }
    }, []);

    // --- Firebase Initialization and Auth ---
    useEffect(() => {
        if (Object.keys(firebaseConfig).length === 0) {
            console.error("Firebase config is empty. App cannot initialize.");
            setError("Application is not configured correctly. Please contact support.");
            setIsLoading(false);
            return;
        }

        try {
            const app = initializeApp(firebaseConfig);
            const authInstance = getAuth(app);
            const dbInstance = getFirestore(app);
            
            // For more detailed debugging logs from Firestore - consider removing in production
            setLogLevel('debug');

            setAuth(authInstance);
            setDb(dbInstance);

            const unsubscribe = onAuthStateChanged(authInstance, async (user) => {
                if (user) {
                    setUserId(user.uid);
                } else {
                    // If no user, try to sign in.
                    try {
                        if (typeof __initial_auth_token !== 'undefined' && __initial_auth_token) {
                            await signInWithCustomToken(authInstance, __initial_auth_token);
                        } else {
                            await signInAnonymously(authInstance);
                        }
                    } catch (authError) {
                        console.error("Error during sign-in:", authError);
                        setError("Could not connect to the scheduling service.");
                    }
                }
                setIsAuthReady(true);
            });

            return () => unsubscribe();
        } catch (e) {
            console.error("Firebase initialization error:", e);
            setError("There was a problem starting the application.");
            setIsLoading(false);
        }
    }, [firebaseConfig]);


    // --- Firestore Data Fetching ---
    useEffect(() => {
        // Ensure db is initialized and user is authenticated
        if (!db || !isAuthReady || !userId) {
            return;
        }

        setIsLoading(true);
        // Path for public data shared among all users of this app instance
        const collectionPath = `artifacts/${appId}/public/data/appointments`;
        const q = query(collection(db, collectionPath));

        const unsubscribe = onSnapshot(q, (querySnapshot) => {
            const appts = [];
            querySnapshot.forEach((doc) => {
                appts.push({ id: doc.id, ...doc.data() });
            });
            // Sort appointments by date and time in memory
            const sortedAppts = appts.sort((a, b) => {
                 // More robust date sorting if date might be missing
                 const dateA = a.date && typeof a.date.toDate === 'function' ? a.date.toDate() : new Date(0);
                 const dateB = b.date && typeof b.date.toDate === 'function' ? b.date.toDate() : new Date(0);

                 if (dateA > dateB) return 1;
                 if (dateA < dateB) return -1;
                 // If dates are same, sort by time
                 return (a.time || "").localeCompare(b.time || "");
            });
            setAppointments(sortedAppts);
            setIsLoading(false);
        }, (err) => {
            console.error("Error fetching appointments:", err);
            setError("Failed to load appointments. Please try again later.");
            setIsLoading(false);
        });

        // Cleanup subscription on unmount
        return () => unsubscribe();

    }, [db, isAuthReady, userId, appId]);

    // --- Form Handling ---
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        // Set submitting state to true
        setIsSubmitting(true);
        setError(null);
        setSuccessMessage('');

        if (!db || !isAuthReady) {
            setError("Cannot submit form. Database not connected.");
            setIsSubmitting(false); // Reset submitting state
            return;
        }
        
        // Basic validation
        if (!formData.name || !formData.address || !formData.date || !formData.time) {
            setError("Please fill out all required fields: Name, Address, Date, and Time.");
            setIsSubmitting(false); // Reset submitting state
            return;
        }

        // Validate date is not in the past
        const selectedDate = new Date(formData.date);
        const today = new Date();
        today.setHours(0, 0, 0, 0); // Normalize today to start of day for comparison
        if (selectedDate < today) {
            setError("Please select a date in the future.");
            setIsSubmitting(false);
            return;
        }

        // Basic email validation (more robust regex can be used)
        if (formData.email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
            setError("Please enter a valid email address.");
            setIsSubmitting(false);
            return;
        }

        // Determine the final service type to save
        const finalServiceType = formData.serviceType === 'Other' ? formData.otherServiceType : formData.serviceType;
        if (formData.serviceType === 'Other' && !finalServiceType) {
            setError("Please specify the 'Other' service type.");
            setIsSubmitting(false);
            return;
        }

        try {
            const collectionPath = `artifacts/${appId}/public/data/appointments`;
            // Convert date string to JavaScript Date object for Firestore
            const appointmentDate = new Date(formData.date);

            await addDoc(collection(db, collectionPath), {
                ...formData,
                serviceType: finalServiceType, // Use the determined service type
                date: appointmentDate,
                createdAt: new Date(),
                status: 'Pending'
            });

            setSuccessMessage('Your appointment has been successfully scheduled! The notary will contact you to confirm.');
            
            // Reset form and otherServiceType
            setFormData({
                name: '', phone: '', email: '', address: '', serviceType: 'General Notary Work', date: '', time: '', otherServiceType: ''
            });

            // Hide success message after a few seconds
            setTimeout(() => setSuccessMessage(''), 8000);

        } catch (err) {
            console.error("Error adding document: ", err);
            setError("There was an issue scheduling your appointment. Please try again.");
        } finally {
            setIsSubmitting(false); // Always reset submitting state
        }
    };
    
    // --- UI Rendering ---
    return (
        <div className="bg-gray-50 min-h-screen font-sans text-gray-800">
            <div className="container mx-auto p-4 md:p-8">

                {/* --- Header --- */}
                <header className="text-center mb-8 md:mb-12">
                    <h1 className="text-4xl md:text-5xl font-bold text-blue-800">Mobile Notary Scheduling</h1>
                    <p className="text-lg text-gray-600 mt-2">Book a professional notary to come to you.</p>
                </header>
                
                <div className="grid grid-cols-1 lg:grid-cols-5 gap-8">

                    {/* --- Left Column: Booking Form --- */}
                    <div className="lg:col-span-2 bg-white p-6 md:p-8 rounded-2xl shadow-lg border border-gray-200">
                        <h2 className="text-2xl font-bold mb-6 text-blue-700">Schedule an Appointment</h2>
                        
                        {/* --- User Messages: Error, Success --- */}
                        {error && <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-4 rounded-md" role="alert">{error}</div>}
                        {successMessage && <div className="bg-green-100 border-l-4 border-green-500 text-green-700 p-4 mb-4 rounded-md" role="alert">{successMessage}</div>}

                        <form onSubmit={handleSubmit} className="space-y-4">
                            {/* Input Fields */}
                            <div>
                                <label htmlFor="name" className="block text-sm font-medium text-gray-700">Full Name</label>
                                <input type="text" name="name" id="name" value={formData.name} onChange={handleInputChange} required className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                            </div>
                             <div>
                                <label htmlFor="address" className="block text-sm font-medium text-gray-700">Full Address for Signing</label>
                                <input type="text" name="address" id="address" value={formData.address} onChange={handleInputChange} placeholder="e.g., 123 Main St, Houston, TX 77002" required className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                            </div>
                            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                <div>
                                    <label htmlFor="phone" className="block text-sm font-medium text-gray-700">Phone Number</label>
                                    <input type="tel" name="phone" id="phone" value={formData.phone} onChange={handleInputChange} className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                                </div>
                                 <div>
                                    <label htmlFor="email" className="block text-sm font-medium text-gray-700">Email Address</label>
                                    <input type="email" name="email" id="email" value={formData.email} onChange={handleInputChange} className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                                </div>
                            </div>
                             <div>
                                <label htmlFor="serviceType" className="block text-sm font-medium text-gray-700">Service Type</label>
                                <select name="serviceType" id="serviceType" value={formData.serviceType} onChange={handleInputChange} className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500">
                                    <option>General Notary Work</option>
                                    <option>Loan Signing</option>
                                    <option>Apostille Services</option>
                                    <option>I-9 Verification</option>
                                    <option>Other</option>
                                </select>
                            </div>
                            {/* Conditional input for "Other" service type */}
                            {formData.serviceType === 'Other' && (
                                <div>
                                    <label htmlFor="otherServiceType" className="block text-sm font-medium text-gray-700">Please Specify Service</label>
                                    <input type="text" name="otherServiceType" id="otherServiceType" value={formData.otherServiceType} onChange={handleInputChange} required className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                                </div>
                            )}
                            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                <div>
                                    <label htmlFor="date" className="block text-sm font-medium text-gray-700">Date</label>
                                    <input type="date" name="date" id="date" value={formData.date} onChange={handleInputChange} required className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                                </div>
                                <div>
                                    <label htmlFor="time" className="block text-sm font-medium text-gray-700">Time</label>
                                    <input type="time" name="time" id="time" value={formData.time} onChange={handleInputChange} required className="mt-1 block w-full px-3 py-2 bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"/>
                                </div>
                            </div>
                            
                            {/* Submit Button - Disabled when submitting */}
                            <button 
                                type="submit" 
                                className="w-full bg-blue-600 text-white font-bold py-3 px-4 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-colors duration-300 shadow-md disabled:opacity-50 disabled:cursor-not-allowed"
                                disabled={isSubmitting} // Disable button while submitting
                            >
                                {isSubmitting ? 'Scheduling...' : 'Request Notary'} {/* Change text based on submitting state */}
                            </button>
                        </form>
                    </div>

                    {/* --- Right Column: Appointments Dashboard --- */}
                    <div className="lg:col-span-3">
                        <div className="bg-white p-6 md:p-8 rounded-2xl shadow-lg h-full border border-gray-200">
                            <h2 className="text-2xl font-bold mb-6 text-gray-700">Upcoming Appointments</h2>
                             {isLoading ? (
                                <div className="flex justify-center items-center h-64">
                                    <div className="animate-spin rounded-full h-16 w-16 border-t-2 border-b-2 border-blue-500"></div>
                                </div>
                            ) : appointments.length === 0 ? (
                                <div className="text-center py-16">
                                    <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true">
                                        <path vectorEffect="non-scaling-stroke" strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                                    </svg>
                                    <h3 className="mt-2 text-sm font-medium text-gray-900">No appointments</h3>
                                    <p className="mt-1 text-sm text-gray-500">There are currently no scheduled appointments.</p>
                                </div>
                            ) : (
                                <div className="space-y-4">
                                    {appointments.map(appt => (
                                        <div key={appt.id} className="p-4 bg-gray-50 rounded-lg border border-gray-200 hover:shadow-md transition-shadow">
                                            <div className="flex flex-col sm:flex-row justify-between">
                                                <div>
                                                    <p className="font-bold text-lg text-blue-800">{appt.name}</p>
                                                    <p className="text-sm text-gray-600">{appt.serviceType}</p>
                                                    <p className="text-sm text-gray-500 flex items-center mt-1">
                                                        <svg className="h-4 w-4 mr-1.5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                                            <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
                                                        </svg>
                                                        {appt.address}
                                                    </p>
                                                </div>
                                                <div className="text-left sm:text-right mt-3 sm:mt-0">
                                                    <p className="font-semibold text-gray-800">{formatDate(appt.date)}</p>
                                                    <p className="text-blue-600 font-bold">{formatTime(appt.time)}</p>
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    </div>
                </div>

                {/* Footer with App ID for support */}
                 <footer className="text-center mt-8 md:mt-12 text-gray-500 text-sm">
                    <p>Powered by a custom scheduling solution.</p>
                    <p className="mt-1">Your App ID: <span className="font-mono bg-gray-200 px-2 py-1 rounded">{appId}</span></p>
                 </footer>

            </div>
        </div>
    );
}